import "core-js/modules/es.array.map.js";
import "core-js/modules/es.object.to-string.js";
import "core-js/modules/es.number.constructor.js";
import Navbar from '@/components/Navbar.vue';
import TutorialCell from '@/components/TutorialCell.vue';

var dayjs = require('dayjs');

export default {
  name: 'WishCalculator',
  components: {
    Navbar: Navbar,
    TutorialCell: TutorialCell
  },
  data: function data() {
    var _this = this;

    return {
      presetTargets: [64, 68, 72, 76, 80, 88, 95, 99].map(function (count) {
        return {
          count: count,
          desc: _this.$t("wish.presets.".concat(count))
        };
      }),
      target: 64,
      targetString: '64',
      nextMinigame: 1,
      showNextMinigameDetail: false,
      freeHad: 0,
      paidHad: 0,
      shop: 20,
      wished: 0,
      lost: 0,
      dailyBought: false,
      packBought: false,
      minDate: dayjs().subtract(5, 'hour').subtract(8, 'day').toDate(),
      maxDate: dayjs().subtract(5, 'hour').toDate(),
      showCalendar: false,
      calendar: new Date(),
      calendarString: dayjs().format(this.$t('wish.dateFormat')),
      result: [],
      showResult: false,
      theme: {
        dialogFontSize: 'var(--van-font-size-md)'
      },
      stepper: {
        integer: true,
        'input-width': '48px'
      }
    };
  },
  methods: {
    calendarConfirm: function calendarConfirm(v) {
      this.showCalendar = false;
      this.calendar = v;
      this.calendarString = dayjs(v).format(this.$t('wish.dateFormat'));
    },
    calculate: function calculate() {
      var _this2 = this;

      var result = [[this.$t('wish.result.type[0]')]];

      var addResultItem = function addResultItem(key, count, star) {
        result.push([_this2.$t("wish.result.".concat(key)), count, star]);
      };

      var minigame = 23 - (this.nextMinigame || 1);
      addResultItem('minigame', minigame);
      var shop = this.shop || 20;
      addResultItem('shop', shop);
      var wished = this.wished || 0;
      var progress = 5;
      [6, 18, 32, 46, 52].forEach(function (bonusTime) {
        if (wished >= bonusTime) progress -= 1;
      });
      addResultItem('progress', progress);
      var freeHad = this.freeHad || 0;
      var paidHad = this.paidHad || 0;
      if (freeHad + paidHad) addResultItem('current', freeHad + paidHad);
      var lost = this.lost || 0;
      if (lost) addResultItem('lost', -lost);
      result.push([this.$t('wish.result.type[1]')]);
      var have = minigame + shop + wished + freeHad + paidHad - lost + progress;
      var need = this.target - have;

      if (need > 0) {
        var dailyBought = this.dailyBought,
            packBought = this.packBought;
        var dailyRemain = 8 - dayjs().subtract(5, 'hour').diff(dayjs.tz(this.calendar), 'day') - Number(dailyBought);
        var star = 0;

        if (need < 4 && dailyRemain >= 0 || need == 4 && dailyRemain >= 4) {
          var daily = Math.min(need, dailyRemain);
          need -= daily;
          star += daily * 2;
          addResultItem('daily', daily, daily * 2);
        } else {
          if (!packBought) {
            need -= 6;
            star += 9;
            addResultItem('pack', 6, 9);
          }

          if (need > 0 && dailyRemain) {
            var _daily = Math.min(need, dailyRemain);

            need -= _daily;
            star += _daily * 2;
            addResultItem('daily', _daily, _daily * 2);
          }

          if (need > 0) {
            star += need * 3;
            addResultItem('direct', need, need * 3);
          }
        }

        result.push([this.$t('wish.result.type[2]')]);
        addResultItem('total', this.target - wished - need, star); // 购买 9 北极星礼盒可能导致星愿瓶溢出 (need < 0)
        // 因此此处减去 need 以得到实际可得抽数
      } else {
        result.push([this.$t('wish.result.type[2]')]);
        addResultItem('total', have - wished, 0);
      }

      this.result = result;
      this.showResult = true;
    }
  }
};